import React, { useContext } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import { useStyles } from "./styles";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import ViewListIcon from "@material-ui/icons/ViewList";
import ViewModuleIcon from "@material-ui/icons/ViewModule";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { Props } from "./types";
import { Context } from "../../context";

export const CustomToolBar: React.FC<Props> = () => {
  const classes = useStyles();
  const { view, setView, setShowContent, showContent } = useContext(Context)!;

  const IconBtn: React.FC<{ onClick?: any }> = ({ onClick, children }) => (
    <IconButton
      edge="start"
      className={classes.menuButton}
      color="inherit"
      aria-label="menu"
      onClick={onClick}
    >
      {children}
    </IconButton>
  );

  const handleChangeView = () => setView(!view);

  const handleBack = () => setShowContent(false);

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar variant="dense">
          {showContent && (
            <IconBtn onClick={handleBack}>
              <ArrowBackIcon />
            </IconBtn>
          )}
          <IconBtn>
            <AddIcon />
          </IconBtn>
          <IconBtn>
            <EditIcon />
          </IconBtn>
          <IconBtn>
            <DeleteIcon />
          </IconBtn>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
            onClick={handleChangeView}
          >
            {view ? (
              <ViewListIcon style={{ transform: "rotate(-180deg)" }} />
            ) : (
              <ViewModuleIcon />
            )}
          </IconButton>
        </Toolbar>
      </AppBar>
    </div>
  );
};
