import React, { useContext } from "react";
import { useStyles } from "./styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Divider from "@material-ui/core/Divider";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import Typography from "@material-ui/core/Typography";
import { Context } from "../../context";
import { get } from "../../utils/api";
import { getImage, generateUID } from "../../utils/helpers";

export const ListComponent = () => {
  const classes = useStyles();
  const { items, setShowContent } = useContext(Context)!;

  const handleClick = (url: string) => {
    setShowContent(true);
    get(url).then((data: any) => console.log("yes"));
  };

  return items ? (
    <List className={classes.root} style={{ margin: "auto" }}>
      {items.map((item: any) => (
        <div key={generateUID()}>
          <ListItem
            alignItems="flex-start"
            className={classes.listItem}
            onClick={() => handleClick("/articles/" + item.slug)}
          >
            <ListItemText
              primary={
                <Typography component="span" variant="h6" color="textPrimary">
                  {item.title}
                </Typography>
              }
              secondary={
                <React.Fragment>
                  <Typography
                    component="span"
                    variant="body2"
                    className={classes.inline}
                    color="textPrimary"
                  >
                    {item.lowSearchText.length > 200
                      ? item.lowSearchText.slice(0, 200) + "..."
                      : item.lowSearchText}
                  </Typography>

                  <Typography variant="overline" display="block" gutterBottom>
                    {item.publishedAt}
                  </Typography>
                </React.Fragment>
              }
            />
            <ListItemAvatar>
              <img
                alt={item.title}
                src={getImage(item, "one-sixth")}
                className={classes.img}
              />
            </ListItemAvatar>
          </ListItem>
          <Divider />
        </div>
      ))}
    </List>
  ) : null;
};
