import React, { useContext } from "react";
import { useStyles } from "./styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { getImage } from "../../utils/helpers";
import { get } from "../../utils/api";
import { Context } from "../../context";
import { MORE } from "../../utils/constants";

export const CardsComponent = () => {
  const classes = useStyles();
  const { items, setShowContent } = useContext(Context)!;

  const handleClick = (url: string) => {
    setShowContent(true);
    get(url).then((data: any) => console.log("yes"));
  };

  return items ? (
    <div className={classes.root}>
      {items.map((item: any, index: number) => (
        <Card
          className={classes.card}
          key={index}
          onClick={() => handleClick("/articles/" + item.slug)}
        >
          <CardActionArea>
            <CardMedia
              component="img"
              className={classes.media}
              src={getImage(item, "two-thirds")}
              title={item.title}
            />
            <CardContent>
              <Typography
                gutterBottom
                variant="h5"
                component="span"
                className={classes.title}
              >
                {item.title}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                {item.lowSearchText.length > 200
                  ? item.lowSearchText.slice(0, 200) + "..."
                  : item.lowSearchText}
              </Typography>
            </CardContent>
          </CardActionArea>
          <CardActions>
            <Button size="small" color="primary">
              {MORE}
            </Button>
          </CardActions>
        </Card>
      ))}
    </div>
  ) : null;
};
