import React, { useEffect, useState } from "react";
import { useStyles } from "./styles";
import { Props } from "./types";
import { get } from "../../utils/api";
import { ListComponent } from "../ListComponent";
import { CardsComponent } from "../CardsComponent";
import { CustomToolBar } from "../CustomToolBar";
import { Context } from "../../context";
import { Content } from "../Content";

export const App: React.FC<Props> = ({ cards, url }) => {
  const [items, setItems] = useState<any>();
  const [view, setView] = useState<boolean>(cards || false);
  const [showContent, setShowContent] = useState<boolean>(false);

  useEffect(() => {
    get(url).then((data: any) => setItems(data.results));
  }, [url]);

  const classes = useStyles();
  return (
    <Context.Provider
      value={{
        items,
        view,
        setView,
        setShowContent,
        showContent,
      }}
    >
      <div className={classes.root}>
        <CustomToolBar />
        <div className={classes.viewer} id="_viewer">
          {showContent ? (
            <Content />
          ) : (
            <>{view ? <CardsComponent /> : <ListComponent />}</>
          )}
        </div>
      </div>
    </Context.Provider>
  );
};
